import {
	uniCard,
	uniNavBar
} from '@dcloudio/uni-ui'
// 也可使用此方式引入组件
import {
	mapState,
	mapMutations
} from 'vuex'
export default {
	components: {
		uniCard,
		uniNavBar
	},
	computed: {
		...mapState(['hasLogin'])
	},

	data() {
		return {
			title: '登 录',
			iseye: false,
			isver: false,
			phone: "",
			wxdata: {}
		}
	},
	onLoad() {

		uni.login({
			provider: 'weixin',
			success: loginRes => {
				uniCloud.callFunction({
					name: 'getUnionid',
					data: {
						js_code: loginRes.code
					}
				}).then((res) => {
					this.wxdata = res.result.data;
				})
			}
		});

	},
	methods: {
		...mapMutations(['login']),
		// 提交验证
		formSubmit: function(e) {
			console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
			var formdata = e.detail.value;
			if (!this.utils.checkPhone(formdata.phone)) {
				uni.showToast({
					title: '手机号输入错误',
					icon: 'none'
				})
				return
			}
			if (formdata.password && Number(formdata.password.length) < 6) {
				uni.showToast({
					title: '密码长度不少于6位',
					icon: 'none'
				})
				return
			}
			let postData = {};
			postData = { ...this.wxdata,
				...formdata
			}
			uniCloud.callFunction({
				name: 'login',
				data: postData
			}).then((res) => {
				if (res.result.status == 0) {
					this.login(res.result.token);
					uni.reLaunch({
						url: './index'
					})
				} else {
					uni.showModal({
						title: '提示',
						showCancel: false,
						content: res.result.msg,
						success: function(res) {
							if (res.confirm) {
								console.log('用户点击确定');
							}
						}
					});
				}
			})
		}
	}
}
